  const [dateObject, setDateObject] = useState({})
  const [gregorian, setGregorian] = useState("")
  const [persian, setPersian] = useState("")
  const [arabic, setArabic] = useState("")
  const [indian, setIndian] = useState("")
  const [format, setFormat] = useState("MM/DD/YYYY")
  const [jsDate, setJsDate] = useState("")

  const [date, setDate] = useState(new Date())
  const [childDate, setChildDate] = useState(new Date())

  const [minMax2, setMinMax2] = useState("2020/12/04")
  const [minMax1, setMinMax1] = useState(new Date().setDate(10))

  let minmaxToday = new DateObject()
  let minmaxTomorrow = new DateObject(minmaxToday).setDay(minmaxToday.day + 1)

  const [minMax3, setMinMax3] = useState([
    minmaxToday,
    minmaxTomorrow
  ])
  const [datesM1, setDatesM1] = useState([today, tomorrow])
  const [datesR1, setDatesR1] = useState([today, tomorrow])
  const [multipleDates, setMultipleDates] = useState([new Date(), new DateObject({ year: 2020, month: 9, day: 8 }), "December 09 2020", 1597994736000])
  const [FL2, setFL2] = useState(new Date())
  const [FL3, setFL3] = useState(new Date())

  useEffect(() => {
    if (dateObject.isValid) {
      setGregorian(dateObject.convert("gregorian").format(format))
      setPersian(dateObject.convert("persian").format(format))
      setArabic(dateObject.convert("arabic").format(format))
      setIndian(dateObject.convert("indian").format(format))
      setJsDate(dateObject.toDate().toString())
    } else {
      setGregorian("")
      setPersian("")
      setArabic("")
      setIndian("")
      setJsDate("")
    }
  }, [dateObject, format])

  useEffect(() => Prism.highlightAll(), [])



  useEffect(() => {
    let scrollToTop = document.querySelector(".scroll-to-top");
    let main = document.querySelector(".main")

    document.addEventListener("click", handleClickOutside)
    scrollToTop.addEventListener("click", toTop)
    main.addEventListener("scroll", activeScrollTop)

    function handleClickOutside(event) {
      if (sidebarRef.current && !sidebarRef.current.contains(event.target) && !event.target.classList.contains("open-sidebar")) {
        setIsActive(false);
      }
    }

    function activeScrollTop() {
      scrollToTop.style.display = main.scrollTop > 50 ? "block" : "none"
    }

    function toTop() {
      main.scrollTop = 0;
    }

    return () => {
      document.removeEventListener("click", handleClickOutside, false)
      scrollToTop.removeEventListener("click", toTop)
      window.removeEventListener("scroll", activeScrollTop)
    }
  }, [])

